import uniCard from '@/components/uni-card/uni-card';
const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();
innerAudioContext.autoplay = true;
export default {
	components: {
		uniCard
	},
	data() {
		return {
			listData: [], //获取到的批改数据
			token: '',
			userDictationaId: -1, //上一页传来的id
			lookObj: {}, //视频预览的数据
			isLook: false, //是否显示视频预览
			correctionType: 0, //上传的模式
			name: '', //上传的标题
			imageUrl: '', //封面地址
			dataUrl:'',//上传的地址
			videoUrl: '', //视频地址
			imgArr: [], //上传的图片数组
			isPrompt: false, //是否显示提示框
			tabIndex: 0, //点击了哪一个模式
			isRecord: false,
			voicePath: '', //录制音频地址
			isPlayShow: false, //是否显示音频播放插件
			isPlay: false //是否为播放状态
		};
	},
	onLoad(e) {
		let self = this;
		const param = {
			hz: 'content', //修改文件格式 找后台要 音频的是 voice 视频video  图片 content   第一个地方
		}
		this.uniHttp.getParamA(param);
		recorderManager.onStop(function(res) {
			console.log(res);
			self.voicePath = res.tempFilePath;
			self.audioUrl = res.tempFilePath;
			this.isPlayShow = true;
		});
		this.token = e.token;
		this.userDictationaId = e.id;
		let url = 'manageHabit/selectHabitCorrection.json';
		const data = {
			userDictationaId: this.userDictationaId,
			token: this.token,
			enable: 1
		};
		return;
		this.uniHttp.getJSON(url, data, res => {
			if (res.data.success) {
				let data = res.data.data;
				if (data != null) {
					console.log('run');
					this.listData = data;
					for (let i = 0; i < data.length; i++) {
						if (data[i].correctionType == 0 || data[i].type == 1) {
							//图片
							let imgArr = [];
							imgArr.push(data[i].workUrl);
							imgArr.push(data[i].twoWork);
							imgArr.push(data[i].threeWorl);
							data[i].imgArr = imgArr;
						} else if (data[i].type == 2) {
							//视频
							let videoArr = [];
							if (data[i].workUrl) {
								let obj = {
									img: data[i].photoUrl,
									video: data[i].workUrl
								};
								videoArr.push(obj);
							} else {
								videoArr.push('');
							}
							if (data[i].twoWork) {
								let obj = {
									img: data[i].twoPhoto,
									video: data[i].twoWork
								};
								videoArr.push(obj);
							} else {
								videoArr.push('');
							}
							if (data[i].threeWorl) {
								let obj = {
									img: data[i].twoPhoto,
									video: data[i].threeWorl
								};
								videoArr.push(obj);
							} else {
								videoArr.push('');
							}
							data[i].videoArr = videoArr;
						}
					}
				}
			}
		});
	},
	onNavigationBarButtonTap(e){
		console.log(e);
		if(e.index==0){
			this.send();
		}
	},
	methods: {
		// 上传批改
		send(){
			if(this.correctionType==0){
				this.uniHttp.appendFileA(this.imgArr[0]);
			}
			let url = 'manageHabit/insertHabitCorrection.json';
			const data = {
				userDictationaId: this.userDictationaId,
				token: this.token,
				enable: 1,
				name:this.name,
				photoUrl:this.imageUrl,
				videoUrl:this.dataUrl,
				correctionType:this.correctionType,
			};
			return;
			this.uniHttp.getJSON(url, data, res => {
				if (res.data.success) {
					
				}
			})
		},
		getParamA(type){
			let url = 'practice/uploadOss.json';
			const data = {
				hz:type,
			}
			this.uniHttp.imgOss(url,data,res=>{
				
			})
		},
		videoErrorCallback() {},
		record(type) {
			if (!type) {
				this.startRecord();
				this.isRecord = true;
			} else {
				this.endRecord();
				this.isRecord = false;
			}
		},
		startRecord() {
			console.log('开始录音');
			uni.showToast({
				type: 'none',
				title: '开始录音'
			});
			recorderManager.start({
				format: 'mp3'
			});
		},
		endRecord() {
			console.log('录音结束');
			uni.showToast({
				type: 'none',
				title: '录音结束'
			});
			this.isPlayShow = true;
			recorderManager.stop();
		},
		playVoice(isPlay) {
			console.log('播放录音');
			if (!isPlay) {
				innerAudioContext.src = this.voicePath;
				innerAudioContext.play();
				this.isPlay = true;
			} else {
				innerAudioContext.pause();
				this.isPlay = false;
			}
		},
		// 上传图片
		uploadImg() {
			//上传图片
			// this.imageUrl
			var _this = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				success: function(res) {
					_this.imgArr = res.tempFilePaths;
				}
			});
		},
		// 上传视频
		uploadVideo() {
			var _this = this;
			if (this.imgArr.length == 0) {
				uni.showToast({
					type: 'none',
					title: '请先上传封面'
				});
			} else {
				uni.chooseVideo({
					count: 1,
					sourceType: ['camera', 'album'],
					success: function(res) {
						_this.videoUrl = res.tempFilePath;
					}
				});
			}
		},
		deleteImg(index) {
			//删除图片
			this.imgArr.splice(index, 1);
		},
		tapPrompt(type) {
			if (this.name != '' || this.imgArr.length != 0 || this.voicePath != '' || this.videoUrl != '') {
				this.tabIndex = type;
				this.isPrompt = true;
			} else {
				this.tapBar(type);
			}
		},
		tapClose() {
			this.isPrompt = false;
		},
		tapBar(type) {
			//切换上传模式
			this.correctionType = type;
			this.name = '';
			this.imgArr = [];
			this.voicePath = '';
			this.videoUrl = '';
			this.isPlay = false;
			this.isRecord = false;
			this.tapClose();
		},
		lookImg(urls, i) {
			//预览图片
			uni.previewImage({
				current: i,
				urls: urls
			});
		},
		lookVideo(img, video, index, i) {
			//预览视频
			if (this.lookObj.index == index && this.lookObj.i == i) {
				this.isLook = false;
			} else {
				this.isLook = true;
				this.lookObj.index = index;
				this.lookObj.i = i;
			}
			if (this.isLook) {
				this.lookObj.img = img;
				this.lookObj.video = video;
			} else {
				this.lookObj = {};
				this.lookObj.index = -1;
				this.lookObj.i = -1;
			}
		},
		close() {
			//关闭视频
			this.isLook = false;
			this.lookObj = {};
		},
		tabPi(list) {
			//点击批改
			uni.navigateTo({
				url: '../peiyoucorrect/peiyoucorrect?id=' + list.id
			});
		}
	}
};
